<template>
  <div class="news_list_group">
    <div class="container-fluid">
      <template v-for="newItem in datas" :key="newItem.id">
        <div class="row">
          <div class="col col-lg-4 col-12">
            <img :src="newItem.thumbnail" />
            <h5 class="category">{{ "【" + newItem.category + "】" }}</h5>
          </div>
          <div class="col col-lg-5 col-md-6 col-12">
            <h5 class="title">{{ newItem.title }}</h5>
          </div>
          <div class="col col-lg-3 col-md-6 col-12">
            <h5 class="time">{{ getDate(newItem.posted_at) }}</h5>
            <i id="boot-icon" class="bi bi-caret-right-fill"></i>
          </div>
        </div>
        <hr />
        <!-- v-if="index != datas.length - 1" -->
      </template>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      datas: [
        {
          id: 15,
          category: "平台公告",
          title: "公告15號",
          thumbnail: "https://fakeimg.pl/400x400/D7BEB2/EEE/", //"http://test.url/news/15/thumbnail.png",
          posted_at: 1665455530,
        },
        {
          id: 14,
          category: "遊戲公告",
          title: "公告14號公告14號",
          thumbnail: "https://fakeimg.pl/400x400/D7BEB2/EEE/", //"http://test.url/news/14/thumbnail.png",
          posted_at: 1665450530,
        },
        {
          id: 13,
          category: "遊戲‧遊戲",
          title: "公告13號公告13號公告13號",
          thumbnail: "https://fakeimg.pl/400x400/93503B/EEE/", //"http://test.url/news/13/thumbnail.png",
          posted_at: 1665440530,
        },
      ],
    };
  },
  methods: {
    getDate: function (timestamp) {
      var date = new Date(timestamp);
      //   console.log(
      //     "Date: " +
      //       date.getDate() +
      //       "/" +
      //       (date.getMonth() + 1) +
      //       "/" +
      //       date.getFullYear() +
      //       " " +
      //       date.getHours() +
      //       ":" +
      //       date.getMinutes() +
      //       ":" +
      //       date.getSeconds()
      //   );
      return (
        date.getMonth() +
        1 +
        "/" +
        date.getDate() +
        " " +
        date.getHours() +
        ":" +
        date.getMinutes()
      );
    },
  },
};
</script>

<style scoped lang="scss">
.news_list_group {
  margin: 15px 20px;
  margin-bottom: 40px;
  hr {
    border-bottom: 1px solid #727171;
    margin: 0;
  }
  h5 {
    margin: unset;
    color: #727171;
    font-weight: unset;
  }
  img {
    height: 50px;
    width: 50px;
    border-radius: 50%;
  }
  .category {
    width: 150px;
    color: #727171;
    font-weight: bold;
    margin-left: 20px;
  }
  .title {
    padding: 10px 0;
  }
  .time {
    text-align: right;
    padding: 10px 0;
    ~ i {
      margin-left: 20px;
    }
  }
  .col {
    display: flex;
    align-items: center;
  }
  .col:has(.time) {
    justify-content: flex-end;
  }
  .row {
    padding: 1rem 0;
    border-radius: 15px;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
    &:hover {
      cursor: pointer;
      background-color: #d3d1d0;
    }
  }
}
</style>
